.menu-site {
  height: 120px;
  background-color: white;
  li{
    font-size: 12px;
    font-weight: 600;
    color: $gray-500;
    text-transform: uppercase;
    margin-left: 15px;
    margin-right: 15px;
    a:hover{
      font-weight: 600;
      color: $primary;
    }
    a:active{
      font-weight: 700;
      color: $primary;
    }
  }
}

.menu-site-mobile {
  height: 80px;
  background-color: white;
  li{
    font-size: 12px;
    font-weight: 600;
    color: $gray-500;
    text-transform: uppercase;
    margin-left: 15px;
    margin-right: 15px;
    a:hover{
      font-weight: 600;
      color: $primary;
    }
    a:active{
      font-weight: 700;
      color: $primary;
    }
    i{
      font-size: 16px;
      line-height: 16px;
      
    }
  }
}

.logo-vila {
  display: block;
  max-width: 85%;
  margin-top: -5px;
}

.header-site {
  min-height: calc(100vh - 120px);
  background-image: url('../images/site/sidekix-media-1vMz2_MclrM-unsplash.jpg');
  background-position: center center;
  background-size: cover;
  h1 {
    font-size: 36px;
    font-weight: 600;
    letter-spacing: -1px;
    color: $primary;
  }
  .card {
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }
}

.container-site {
  min-height: 100vh;
}

.card-imoveis-title{
  font-size: 24px;
  font-weight: 600;
}

.card-imoveis {
  .card {
  border: 1px solid #CCCBCF;
  box-sizing: border-box;
  }
}

.card-imoveis:hover {
  .card {
  border: 1px solid #F5F5F7;
  box-sizing: border-box;
  box-shadow: 0px 7px 25px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  }
  .btn-card-imoveis {
    display: block;
  }
  .imovel-eye {
    display: block!important;
  }
}

.imovel-eye {
  display: none;
  font-size: 22px;
}

.imovel-eye:hover {
  color: $primary;
}

.li-card-imoveis{
  font-size: 13px;
  letter-spacing: -0.75px;
}

.detalhes{
  font-size: 12px;
  font-weight: 700;
  color: #50649C;
  text-transform: uppercase;
}

.detalhes:hover{
  color: $primary;
}

.rounded-imoveis{
  border-radius: 8px;
}

.rounded-imoveis-img{
  border-radius: 8px 8px 0px 0px;
}

.rounded-imoveis-img-list{
  border-radius: 8px 0px 0px 8px;
}

.footer-site {
  height: 20vh;
  background-color: $primary;
  .logo-footer {
    max-width: 200px;
  }
}

.list-footer ul li a{
  color: $gray-300;
  font-size: 12px;
  line-height: 1.25rem;
}

.list-footer ul li a:hover{
  color: $gray-500;
}

.card-imoveis-search {
  .card {
    background-color: $primary;
    border: 1px solid $primary;
    box-sizing: border-box;
    label {
      font-family: Work Sans;
      font-style: normal;
      font-weight: bold;
      font-size: 13px;
      line-height: 16px;
      letter-spacing: -0.03em;
      color: #FFFFFF;
    }
  .switch label {
    font-family: Work Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 24px;
    color: #FFFFFF;
  }
  }
}

.card-imoveis-caracteristicas {
  .card {
    border: 1px solid #CCCBCF;
    box-sizing: border-box;
    }
    label {
      font-family: Work Sans;
      font-style: normal;
      font-weight: bold;
      font-size: 13px;
      line-height: 16px;
      letter-spacing: -0.03em;
      color: $primary;
    }
    .custom-checkbox label {
      font-family: Work Sans;
      font-style: normal;
      font-weight: normal;
      font-size: 13px;
      line-height: 24px;
    }
}

.card-img-list {
  width: 330px;
  min-height: 256px;
  background-image: url(../images/site/greg-rivers-rChFUMwAe7E-unsplash.jpg);
  background-position: center;
  background-size: cover;
}

.card-img-list2 {
  width: 330px;
  min-height: 256px;
  background-position: center;
  background-size: cover;
}

.card-img-list-xl {
  width: 440px;
  min-height: 256px;
  background-image: url(../images/site/greg-rivers-rChFUMwAe7E-unsplash.jpg);
  background-position: center;
  background-size: cover;
}

.btn-card-imoveis {
  display: none;
  z-index: 1;
}

.imovel-title {
  font-family: Work Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: -0.03em;
  color: $primary;
}

.imovel-price {
  font-family: Work Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  color: #696A69;
}

.imovel-text {
  font-family: Work Sans;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  color: #696A69;
}

.icon-card {
  width: 33px;
  height: 33px;
  display: flex;
  align-items: center;
  background-color: $primary;
  border-radius: 5px;
  padding-right: 9px;
  cursor: pointer;
  i {
    font-size: 21px;
    color: #FFFFFF;
    margin-left: 6px;
  }
}

.icon-card:hover {
  background-color: $gray-400;
  i {
    color: $gray-700;
  }
}

.icon-on {
  background-color: $gray-400;
  i {
    color: $gray-700;
  }
}

.icon-off {
  background-color: $primary;
  i {
    color: #ffffff;
  }
}

.detalhes-imoveis {
  margin-top: -38px;
  h1{
    color: $primary;
    letter-spacing: -0.03em;
  }
  h4 a{
    font-size: 16px;
    font-weight: bold;
  }
  ul li{
    font-size: 15px;
  }
}

.detalhes-imoveis-card-1 {
  .card-body{
    background: #FFFFFF;
    border: 1px;
    border-style: solid;
    border-color: #CCCBCF;
    border-radius: 0px 0px 8px 8px;
  }
}

.detalhes-imoveis-card-2 {
  .card-body{
    border: 1px;
    border-style: solid;
    border-color: #CCCBCF;
    border-radius: 8px;
    background: $primary;
  }
}

.btn-tour {
  position: absolute;
  margin-top: 30px;
  margin-left: 30px;
}

.item-galeria {
  a {
    display: inline-block;
  }
  &:hover {
    .item-mask {
      opacity: 0.8;
      visibility: visible;
      .item-caption {
        bottom: 20px;
        opacity: 1;
      }      
    }
    .item-container {
      transform: scale(1.1);
      transition: all 2s cubic-bezier(0.23, 1, 0.32, 1) 0s;
      width: 100%;
    }
  }
}

.numero-fotos {
  top: -50px;
  right: 15px;
  position: relative;
  opacity: 0.75;
  .btn-fotos {
    padding: 5px 10px;
    border-width: 2px;
    border-radius: 5px;
    border-style: solid;
    border-color: #FFFFFF;
    color: #FFFFFF;
    font-weight: bold;
  }
}

.btn-multimidia {
  top: -50px;
  left: 5px;
  position: relative;
}

.data-visita {
  padding: 5px 5px;
  border-width: 2px;
  border-radius: 5px;
  border-style: solid;
  border-color: #737079;

  :hover {
    padding: 5px 5px;
  border-width: 2px;
  border-radius: 5px;
  border-style: solid;
  border-color: #737079;
  background-color: $primary;
  color: #FFFFFF;
  }
}

.data-visita-active {
  padding: 5px 5px;
  border-width: 2px;
  border-radius: 5px;
  border-style: solid;
  border-color: #737079;
  background-color: $primary;
  color: #FFFFFF;
}

.hr-steps-active {
  width: 10px;
  border: 1px;
  border-style: solid;
  border-color: #737079;
  margin-left: 10px;
  margin-right: 10px;
}

.hr-steps-inactive {
  width: 10px;
  border: 1px;
  border-style: solid;
  border-color: $gray-400;
  margin-left: 10px;
  margin-right: 10px;
}

.step-active {
  color: #737079;
}

.step-inactive {
  color: $gray-400;
}

.chevron-inactive {
  opacity: 0;
}

.input-caracterisicas-imovel {
  width: 40px;
  height: 25px;
  padding: 2px;
  text-align:center;
  margin-top: -3px;
}

.crop-capa {
  width: 600px;
  height: 450px;
  overflow: hidden;
}

.crop-capa img {
  margin-top: -50%;
  width: 100%;
  height: auto;
}

.crop {
    width: 330px;
    height: 150px;
    overflow: hidden;
    position: relative;
    background-color: #333;
}

.crop img {
  width: 100%;
  min-height: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.crop-capa-2 {
  width: 400px;
  height: 52vh;
  overflow: hidden;
  border: #F9F9F9 solid;
  border-top-width: 2px;
  border-right-width: 9px;
  border-bottom-width: 0px;
  border-left-width: 1px;
}

.crop-capa-2 img {
  width: 100%;
  min-height: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.crop-2 {
    width: 330px;
    height: 26vh;
    overflow: hidden;
    position: relative;
    background-color: #333;
    border: #F9F9F9 solid;
    border-top-width: 2px;
    border-right-width: 1px;
    border-bottom-width: 0px;
    border-left-width: 1px;
  }

.crop-2 img {
  width: 100%;
  min-height: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.dropdown-menu {
  margin-top: 10px;

}

@media (max-width: 768x) {
    
}


@media (max-width: 576px) {

.menu-site {
  display: none!important;
}

.logo-vila {
  display: block;
  max-width: 100%;
}

.header-site {
  min-height: calc(70vh - 80px);
  background-image: url('../images/site/sidekix-media-1vMz2_MclrM-unsplash.jpg');
  background-position: center center;
  background-size: cover;
  h1 {
    font-size: 20px;
    line-height: 20px;
    font-weight: 600;
    letter-spacing: -1px;
    color: $primary;
  }
  .card {
    margin-top: 20px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }
}
}

@media (min-width: 576px) {

  .menu-site-mobile {
    display: none!important;
  }
}