.expand-button {
	position: relative;
}

.accordion-toggle .expand-button:after
{
  position: absolute;
  top: 48%;
  transform: translate(0, -50%);
  font-size: 26px;
  line-height: 26px;
  font-weight: 400;
  content: '-';
}
.accordion-toggle.collapsed .expand-button:after
{
  content: '+';
}

